<template>
  <div>
    <label class="choose">Choose The Check Type</label>

    <label>
      <input type="radio" value="id" v-model="checkType"> ID
    </label>

    <label>
      <input type="radio" value="name" v-model="checkType"> Name
    </label>

    <input v-if="checkType == 'id'" class="checkInput" type="number" v-model="searchWithId" placeholder="Enter an Id">
    <input v-else class="checkInput" type="text" v-model="searchWithName" placeholder="Enter a Name">
    <transition 
        enter-active-class="animate__animated animate__bounceInLeft"
        appear
    >
      <table>
        <tr>
          <th>Student's ID</th>
          <th>Student's Name</th>
          <th>Updata / Remove</th>
        </tr>
          <tbody>
            <tr v-for="(stud, index) in filterResult" :key="index">
              <td>{{ stud.id }}</td>
              <td>{{ stud.name }}</td>
              <td>
                <button id="delete" @click="deleteStudent(index)">Delete</button>
                <router-link to="/">
                  <button  id="update" @click="sendInfoIntoMain(index)">Update</button>
                </router-link>
              </td>
            </tr>

            <tr v-if="filterResult.length == 0">
              <td style="padding: 20px 0;" colspan="3">There're no Students</td>
            </tr>
          </tbody>
      </table>
  </transition>  
  </div>
</template>

<script>
import { allFunc } from "../main";

export default {
  name: "Records",

  data: function() {
    return {
      checkType: 'id',
      studentList: allFunc.studentList,
      searchWithId: '',
      searchWithName: ''
    };
  },

  beforeCreate() {
    let localInfo = JSON.parse(localStorage.getItem("myAllStudents"));
    if (localInfo != null) {
      allFunc.studentList = localInfo;
    }
  },
  
  computed: {
    filterStudentWithId(){
      return this.studentList.filter( (el) => {
        return el.id.match(this.searchWithId)
      })
    },
    filterStudentWithName(){
      return this.studentList.filter( el => el.name.match( new RegExp(this.searchWithName, "i") ) )
    },
    filterResult(){
      return this.checkType == 'id'? this.filterStudentWithId :  this.filterStudentWithName
    }
  },

  methods: {
    deleteStudent(index) {
      this.studentList.splice(index, 1);
      localStorage.setItem("myAllStudents", JSON.stringify(this.studentList));
    },
    sendInfoIntoMain(index) {
      allFunc.getInfo(index);
    }
  }
};
</script>

<style lang="scss" scoped>
// .fade-enter{opacity: 0;}
// .fade-enter-active{transition: opacity .5s;}
// .fade-leave-active{
//   transition: opacity .5s;
//   opacity: 0;
// }

div {
  width: 60%;
  margin: 50px auto;
  label{
    &.choose{
      display: block;
      font-size: 20px;
      color: #009688;
      font-weight: bold;
      margin: 15px 0;
    }
    &:not(.choose){
      font-size: 18px;
      font-weight: bold;
      color: #3e3e3e;
      margin-right: 15px;
      cursor: pointer;
    }
  }
  input{
    &.checkInput{
      width: 100%;
      display: block;
      margin: 30px 0;
      padding: 8px;
      border: 2px solid #d4d4d4;
      border-radius: 5px;
      outline: none;
    }
  }
  table {
    @extend %border;
    width: 100%;
    background-color: #f3f3f3;
    & > tr {
      th {
        @extend %border;
      }
    }
    tbody {
      tr {
        td {
          text-align: center;
          @extend %border;
          &:not(:last-of-type) {
            word-break: break-all;
          }
          button {
            border: none;
            outline: none;
            color: #fff;
            padding: 5px 20px;
            border-radius: 4px;
            margin: 5px;
            cursor: pointer;
            &#delete {
              background-color: #e91e63;
            }
            &#update {
              background-color: #009688;
            }
          }
        }
      }
    }
  }
}
%border {
  border: 1px solid #000;
  border-collapse: collapse;
  padding: 5px;
}
</style>
