<template>
  <div id="parent">
    <form class="addStudentForm">
      <div>
        <label>Student's Name</label>
        <input
          type="text"
          placeholder="Type a student's name"
          required="required"
          v-model="inputName"
        />
      </div>
      <div>
        <label>Student ID</label>
        <input
          type="number"
          placeholder="Type a student's ID"
          required="required"
          v-model="inputId"
        />
      </div>
      <button v-if="add" @click.prevent="addStudent">Add</button>
      <button v-else @click.prevent="updateInfo">Update</button>
    </form>
    <div>
      <span v-if="errors">{{errors}}</span>
    </div>
  </div>
</template>

<script>
import { allFunc } from "../main";

export default {

  name: "Add",

  data: function() {
    return {
      inputName: null,
      inputId: null,
      add: true,
      update: allFunc.updataStudent,
      index: null,
      errors: null
    };
  },

  created() {
    let localInfo = JSON.parse(localStorage.getItem("myAllStudents"));
    if (localInfo != null) {
      allFunc.studentList = localInfo;
    }

    if (this.update != null) {
      this.inputName = this.update.name;
      this.inputId = this.update.id;
      this.add = false;
    }
  },

  methods: {
    addStudent() {
      let subThis = this;
      if(subThis.inputName && subThis.inputId){
        allFunc.addStudent(subThis.inputName, subThis.inputId);
        subThis.clearInput();
        subThis.errors = null;
      }else{
        this.errors = "You Can't Leave These Fields Empty, Please Type The Information !";
      }
    },
    updateInfo() {
      allFunc.finalUpdate(this.inputName, this.inputId);
      this.add = true;
      allFunc.updataStudent = null;
      this.clearInput();
    },
    clearInput() {
      this.inputId = null;
      this.inputName = null;
    }
  }
};
</script>

<style lang="scss" scoped>
#parent{
  form {
    background-color: #f3f3f3;
    padding: 25px;
    margin: 50px auto;
    width: 60%;
    border-radius: 5px;
    position: relative;
    min-height: 285px;
    div {
      margin: 25px 0;
      label {
        display: block;
        margin: 8px 0;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 15px;
      }
      input {
        padding: 7px 10px;
        width: 100%;
        outline: none;
      }
    }
    button {
      position: absolute;
      right: 25px;
      bottom: 25px;
      background-color: #607d8b;
      color: #fff;
      padding: 8px 40px;
      border: none;
      outline: none;
      cursor: pointer;
    }
  }
  div{
    span{
      margin: auto;
      width: 60%;
      display: block;
      background-color: #ffeaf1;
      color: #E91E63;
      padding: 20px;
      text-align: center;
    }
  }
}
</style>
